	let _this
	export default {
		data() {
			return {
				index: 0,
				array: ['ERC20','OMNI'],
				form:{
					usdt:'',
					usdtType:'',
					payPassword:'',	//支付密码
                    qrCode:''
				},
				ratio:'',
				pictures:''
			};
		},
		onLoad:function(){
			_this  = this
			this.fetch()
			
		},
		methods:{
			async fetch(){
				const res  = await this.$http.get('/user/details')
				console.log(res)
				
				this.form.qrCode = res.data.data.qrCode
				this.form.usdt = res.data.data.usdtAddress
				this.form.usdtType = res.data.data.usdtType
				this.array.forEach((item,index)=>{
					if (item == this.form.usdtType){
						this.index = index
					}
				})
				
			},
			showPicture:function(src){
				uni.previewImage({
					urls: [src],
					success:function(){
						console.log('正在预览中')
					}
					
				})
			},
			uploadUSDT:function(){
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
				    success: (chooseImageRes) => {
				        const tempFilePaths = chooseImageRes.tempFilePaths;
				        _this.pictures = ''
								 
						  uni.uploadFile({
							  url: 'http://test.144f.com:8080/sl-web'+'/basicdata/upload', 
							  filePath: tempFilePaths[0],
							  name:'file',
							  success: async (uploadFileRes) => {
								  console.log(uploadFileRes);	
								  _this.pictures=JSON.parse(uploadFileRes.data).data.msg
								  _this.form.qrCode = JSON.parse(uploadFileRes.data).data.msg
								  uni.showToast({
									title: '上传成功',
									icon:'none',
									duration: 1000
								  });
								  // let data ={
									 //  icon: _this.pictures
								  // }
								  // const res = await this.$http.get('/user/update_icon',{data: data})
								  // console.log(res)
							  }
						 });
					}
									 
									  
				})        
			},
			bindPickerChange: function(e) {
			    console.log('picker发送选择改变，携带值为', e.target.value)
			    this.index = e.target.value
			},
			
			async submit(){
				this.form.usdtType = this.array[this.index]
				const res = await this.$http.get('/user/bind_usdt',{data: this.form})
				uni.hideLoading()
				
				uni.showToast({
					title: '保存成功',
					icon: 'none',
					duration: 1000
				});
				setTimeout(function() {
					uni.navigateBack({
						delta: 1
					});
				}, 2000);

			},
			
		}
	}
